.main-header {
  --header-height: 7rem;
  position: -webkit-sticky;
  position: sticky;
  z-index: 999;
  top: 0;
  padding: 0 0.3em;
  background: var(--color-light-1);
  height: var(--header-height);
  width: 98vw;
